<template>
  <div>
    <van-nav-bar
      title="新建习惯提醒"
      left-text=""
      left-arrow
      @click-left="onClickLeft"
      class="shawdow"
    />
    <div class="logoWrapper">
      <img class="logo" src="../assets/1.gif" />
    </div>
    <van-cell-group class="form">
      <van-field
        v-model="title"
        label="推送标题"
        size="large"
        placeholder="请输入标题"
        required
      />
      <van-field
        v-model="content"
        label="推送内容"
        size="large"
        placeholder="请输入内容"
        required
      />

      <van-field
        v-model="time"
        label="推送时间"
        size="large"
        required
        @focus="selectTime"
        placeholder="选择时间(每天一推)"
      />

      <van-field
        v-model="cron"
        label="cron"
        size="large"
        @blur="verifyCron"
        placeholder="可选"
      />
      <van-popup v-model="show" position="bottom">
        <van-datetime-picker
          v-model="currentTime"
          type="time"
          @confirm="getTime"
          @cancel="cancel"
        />
      </van-popup>
    </van-cell-group>
    <div class="submit">
      <van-button
        type="primary"
        @click="newHabit"
        class="btn"
        :block="true"
        size="normal"
        >新建</van-button
      >
    </div>
  </div>
</template>

<script>
import axios from "@/axios";
import mixin from "@/mixins/mixin.js";
import mixinMethod from "@/mixins/mixinMethod.js";
export default {
  mixins: [mixin, mixinMethod],
  name: "NewHabit",
  data() {
    return {
      title: "习惯提醒",
      content: "",
      time: "",
      cron: null,
      minHour: 10,
      maxHour: 20,
      currentTime: "07:00",
      show: false,
      radio: 1
    };
  },
  methods: {
    formatter(value) {
      console.log(value);
      this.time = value;
    },
    getTime(value) {
      this.time = value;
      console.log(value);
      this.show = false;
    },
    newHabit() {
      if (this.content == "" || this.time == "" || this.title == "") {
        this.$toast("必填信息不能为空");
        return;
      }
      let frequency = null;
      let cron = null;
      if (this.cron === null) {
        let time = this.time;
        let arr = time.split(":");
        let hour = arr[0];
        let min = arr[1];
        cron = "0 " + min + " " + hour + " * * ?";
        frequency = 2;
      } else {
        frequency = 6;
        cron = this.cron;
      }
      console.log(cron);
      axios({
        method: "post",
        url: "/api/habit_job",
        data: {
          title: this.title,
          content: this.content,
          // pushTime: this.time,
          frequency: frequency,
          type: 2,
          cron: cron
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.content = "";
          this.time = "";
          this.cron = "";
          this.$toast("新建成功");
        }
      });
    }
  }
};
</script>

<style scoped></style>
